<script>
import Lottie from "@/components/Lottie";
import store from "@/store/index.js";
import dataArtistas from "@/artistas.json";
import menugalery from "@/components/menuGalery";
export default {
  name: "Galery",
  components: {
    //  Aqui instancias todos tus componentes que vas a usar si
    // importas uno y no lo usas te saldra error
    menugalery,
    lottie: Lottie,
  },
  // en la data van todas tus variables
  data() {
    return {
      show: true,
      items: dataArtistas,
      activoPers: 0,
      // dataArtistas[0].url --  asi jalo la info de un solo artista
      // segun la posicion de un arreglo
      imagen: dataArtistas[0].url,
      Nombre: dataArtistas[0].nombre,
      Pais: dataArtistas[0].pais,
      imagenPerfil: dataArtistas[0].perfilUrl,
      ilustradora: dataArtistas[0].ilustradora,
      Redes: {
        facebook: dataArtistas[0].facebook,
        instagram: dataArtistas[0].instagram,
        behance: dataArtistas[0].behance,
        website: dataArtistas[0].web,
      },

      imagenPerfilmobile: "",
      Paismobile: "",
      infoHistoriamobile: "",
      Nombremobile: "",

      PreHistoria: "",
      vrmass: false,
      infoHistoria: "",
      imgMobile: "",
      ilustradoramobile: "",
    };
  },
  // watch hace que cada vez que la variable de activoPers es cambiada
  // se ejecute una funcion que lo que hace es ocultar y mostrar nueva informacion
  watch: {
    activoPers: function (val) {
      // val es el nuevo valor que se cambio en este caso es el
      // stado activo de el artista que se esta mostrando

      // oculto la info del artista anterior
      this.show = false;

      // doy un espacio de tiempo para que entre el nuevo componente
      // con el nuevo artista
      setTimeout(() => {
        this.show = true;
        // inyecto la nueva info del nuevo artista
        (this.Nombre = dataArtistas[val].nombre),
          (this.imagen = dataArtistas[val].url);
        this.imagenPerfil = dataArtistas[val].perfilUrl;
        this.Pais = dataArtistas[val].pais;
        this.PreHistoria = dataArtistas[val].preHistoria;
        this.ilustradora = dataArtistas[val].ilustradora;
        this.Redes.facebook = dataArtistas[val].facebook;
        this.Redes.instagram = dataArtistas[val].instagram;
        this.Redes.behance = dataArtistas[val].behance;
        this.Redes.website = dataArtistas[val].web;
      }, 1500);
      console.log(this.show);
    },
    // cada vez que la pregunta cambie, esta función será ejecutada
  },
  // aqui van todas tus funciones que se ejecutan con @click o mounted
  methods: {
    imagenmobile: function (id) {
      this.imgMobile = dataArtistas[id].url;
      this.ilustradoramobile = dataArtistas[id].ilustradora;
    },
    histomobile: function (id) {
     
this.imagenPerfilmobile = dataArtistas[id].perfilUrl;
this.Paismobile = dataArtistas[id].pais
this.infoHistoriamobile = dataArtistas[id].preHistoria + dataArtistas[id].historia;
this.Nombremobile =dataArtistas[id].nombre
      // imagenPerfilmobile: "",
      // Paismobile: "",
    
      // infoHistoriamobile: "",
      // Nombremobile: "",
    },
    leerDato: function () {
      this.activoPers = store.state.activo;
      this.imagen = dataArtistas[this.activoPers].url;
      this.PreHistoria = dataArtistas[this.activoPers].preHistoria;
      this.Pais = dataArtistas[this.activoPers].pais;
      this.imagenPerfil = dataArtistas[this.activoPers].perfilUrl;
      this.ilustradora = dataArtistas[this.activoPers].ilustradora;
      this.Nombre = dataArtistas[this.activoPers].nombre;
      this.vermas = false;
    },
    sabermas: function () {
      console.log("hola mundo");
      this.vrmass = true;
      console.log(this.vrmass);
      this.infoHistoria =
        this.PreHistoria + dataArtistas[this.activoPers].historia;
    },
    saberHistoria: function (id) {
      console.log(id);
      this.activoPers = id;
      this.infoHistoria = this.PreHistoria + dataArtistas[id].historia;
    },
  },
  // aqui ejecutas una funcion cada vez que se renderize el componente
  mounted() {
    this.leerDato();
  },
  computed: {},
};
</script>

